*{
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}
.header{
    background-image: url(/INDICORP/static/media/background.396e330f.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
}
.header .logo{
    font-weight: bold;
    font-size: 1.5rem;
    color: #252B42 !important;
    margin-right:10%;
}
.header .navbar{
    padding-top: 3%;
    margin-left: 7%;
}
.header .navbar .Navlink{
    font-weight: 600;
    color: #737373 !important;
    margin: 0 10px;
}
.header .navbar .Navlink:hover{
    color: #FFA62B !important;
}

.info{
    margin-left: 14.5%;
    margin-top: 4%;
}
.info h1{
    font-weight: bolder;
    font-size: 58px;
    color: #252B42;
    margin-bottom: 2.5%;
    margin-right:60%;
}
.info p{
    padding-bottom: 3%;
}
.info .firstbutton{
    background: #FFA62B;
    color: #fff;
    font-size: 1rem;
    font-weight: bold;
    padding:15px 25px;
    border-radius: 50px;
    text-decoration: none;
    margin-right: 10px;
    margin-bottom: 10%;
    transition: all .5s ease;
}
.info .firstbutton:hover{
    background: #fd6017;
}
.info .secondbutton{
    background: none;
    color: #00A0C1;
    border: 1px solid #00A0C1;
    border-radius: 50px;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    padding:15px 25px;
    margin-left: 10px;
    margin-bottom: 10%;
    transition: all .5s ease;
}
.info .secondbutton:hover{
    background: #00A0C1;
    color: #ffffff;
    border: 1px solid #ffffff;
    /* border: 1px solid #00A0C1; */
}

.card1{
    text-align: center;
    padding: 5% 6%;
}
.card1 .textcard .cardbox{
    background-color: #fff;
    text-align: left;
    margin:2%;
    width: 80%;
    padding: 3% 4%;
    position: relative;
    top: 10%;
    box-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);

}
.card1 .textcard .cardbox img{
    padding-bottom: 5%;
}
.card1 .textcard .cardbox h3{
    color: #252B42;
    font-weight: bold;
    font-size: 24px;
}
.card1 .textcard .cardbox p{
    color: #737373;
    font-weight: 500;
}
.card1 .textcard .textcol-3{
    background-color: #00A0C1;
}
.card1 .textcard .textcol-3 h3{
    color: #fff;
}
.card1 .textcard .textcol-3 p{
    color: #fff;
}

.container2{
    text-align: center;
    background: #FAFAFA;
    padding-bottom: 5%;
}
.container2 .content{
    padding-top: 8%;
    margin-left: 30%;
    margin-right: 30%;
}
.container2 .content h2{
    color: #252B42;
    font-weight: 800;
    font-size: 40px;
    line-height: 57px;
}
.container2 .content p{
    color: #737373;
    letter-spacing: 0.2px;
    font-weight: 500;
    font-size: 18px;
    margin-top: 2%;
    margin-bottom: 8%;
}
.cards .row{
    margin-left: 9%;
    margin-right: 9%;
}
.cards .row .col{
    background: #ffff;
    margin-left: 2%;
    margin-right: 2%;
    border: 1px solid rgb(245, 245, 245);
    transition: all .5s ease;
}
.cards .row .col:hover{
    -webkit-transform: translateY(-0.4rem);
            transform: translateY(-0.4rem);
    transition: -webkit-transform 200ms;
    transition: transform 200ms;
    transition: transform 200ms, -webkit-transform 200ms;
    background-color: #97e8f8;
    
}
.cards .row .col .title{
    text-align: left;
    padding-top: 9%;
    padding-right: 30%;
}
.cards .row h5{
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1px;
    color: #252B42;
}
.cards .row p{
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #737373;
}
